.app{
    height: 100vh;
    display: flex;
    background-color: #020230;
    color: white;
}
.main{
    width: 75%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #020230),
    url("./assets/bg.jpg") center;
    background-size: cover;
    display: flex;
    flex-direction: column;
}
.top{
    height: 50%;
    position: relative;
}
.timer{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 5px solid rgb(199, 155, 10);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    font-weight: 700; 
    position: absolute;
    bottom: 10px;
    left: 80px;
}
.bottom{
    height: 50%;
}
.trivia{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.question{
    width: 85%;
    background: linear-gradient(rgb(2, 11, 137),black);
    border: 2px solid white;
    text-align: center;
    padding: 20px;
    border-radius:10px;
    font-size:25px;
}
.answers{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.answer{
    width: 40%;
    background: linear-gradient(rgb(2, 11, 137),black);
    border: 1px solid white;
    border-radius: 15px;
    font-size: 20px;
    font-weight: 300;
    text-align: center;
    padding: 12px;
    margin-left: 12px;
    margin-right: 12px;
    margin-bottom: 30px;
}
.answer:hover, 
.answer.active{
    background: mediumblue;
}
.answer.correct{
    animation: correct 3s ease forwards;
}
@keyframes correct{
    0%,22%,42%{
        background: mediumblue;
    }
    20%,40%,60%{
        background: linear-gradient(rgb(2, 11, 137),black);
    }
    62%,100%{
        background: rgb(7, 106, 7);
    }
}
.answer.wrong{
    animation: wrong 3s ease forwards;
}
@keyframes wrong{
    0%,22%,42%{
        background: mediumblue;
    }
    20%,40%,60%{
        background: linear-gradient(rgb(2, 11, 137),black);
    }
    62%,100%{
        background: rgb(153, 6, 6);
    }
}
.pyramid{
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 3px solid rgb(199, 155, 10);

}
.moneyList{
    list-style: none;
    width: 100%;
    padding: 20px;
}
.moneyListItem{
    display: flex;
    align-items: center;
    padding: 11px;
}
.moneyListItem.active{
    background-color: rgb(199, 155, 10);
    padding: 5px;
    border-radius: 5px;
}
.moneyListItemNumber{
    font-size: 20px;
    font-weight: 400;
    width: 30%
}
.moneyListItemAmount{
    font-size: 20px;
    font-weight: 400;
}
.endText{
    position: relative;
    margin:auto;
}
 .start{
    width: 2500px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    margin:auto;
    justify-content: space-around;

} 
.startInput{
    width: 350px;
    height: 42px;
    padding: 4px;
    background-color:rgb(199, 155, 10);
    color:rgb(2, 11, 137);
    border: none;
    border-radius: 5px;
    text-align: center;
    font-size: 22px;
   
    
}
.startInput:focus{
    outline: none;
}
.startButton{
    margin-top: 30px;
    width: 8%;
    height: 42px;
    padding: 4px;
    border: none;
    border-radius: 5px;
    background-color:rgb(199, 155, 10);
    color: rgb(2, 11, 137);
    font-size: 22px;
    font-weight: 500;
    cursor: pointer; 

}